import tuiIcon from "@/components/icon/icon"
import tuiTimeAxis from "@/components/time-axis/time-axis"
import tuiTimeaxisItem from "@/components/timeaxis-item/timeaxis-item"
export default {
	components:{
		tuiIcon,
		tuiTimeAxis,
		tuiTimeaxisItem
	},
	data() {
		return {
			stepList: [{
				name: "preface",
				state: 1,
				stateText: "",
				desc: ["计划前三套模板：新闻、商城、聊天。完成后再逐步追加，敬请期待！"],
				page: "news"
			}, {
				name: "新闻模板",
				state: 1,
				stateText: "（100%）",
				desc: ["新闻模板包含：新闻列表，新闻详情，评论等"],
				page: "news"
			}, {
				name: "聊天模板",
				state: 1,
				stateText: "（100%）",
				desc: ["聊天模板包含：消息列表，好友列表，聊天界面等"],
				page: "msgList"
			}, {
				name: "商城模板",
				state: 0,
				stateText: "（进度85%）",
				desc: ["商城模板包含：商品列表，商品搜索，商品详情，购物车，结算页面，我的订单等"],
				page: "mall"
			}, {
				name: "其它模板",
				state: 0,
				stateText: "",
				desc: ["更多内容敬请期待！"],
				page: "news"
			}]
		}
	},
	methods: {
		template: function(e) {
			let index = e.currentTarget.dataset.index;
			let state = this.stepList[index].state;
			let page = this.stepList[index].page;
			if (state == 1 || index == 3) {
				uni.navigateTo({
					url: "../" + page + "/" + page //`../${page}/${page}`
				})
			} else {
				this.tui.toast("功能开发中~")
			}
		},
		currentStep: function() {
			uni.navigateTo({
				url: '../msgList/msgList'
			})
		}
	}
}
